import React from "react";
import { Link } from "react-router-dom";
import "../includes/style/style.css";
import cs50 from "../includes/img/cs50x.svg";
import municipality from "../includes/img/municipality.svg";

function Footer() {
    return (
        <div>
            <footer>
                <div className="credsgrid">
                    <div>
                        <img src={cs50} alt="" />
                    </div>
                    <div>
                        <img src={municipality} alt="" />
                    </div>
                </div>
                <div className="grid">
                    <ul>
                        <h4>Useful Websites</h4>
                        <li>
                            <a href="https://www.leader.ir/">
                                Leadership of Islamic Republic of Iran
                            </a>
                        </li>
                        <li>
                            <a href="https://president.ir/">Presidency</a>
                        </li>
                        <li>
                            <a href="http://gonbadcity.ir/">Gonbad e Kavous City Municipality</a>
                        </li>
                        <li>
                            <a href="https://gonbad.golestanp.ir/">Gonbad e Kavous Governorship</a>
                        </li>
                    </ul>
                    <ul>
                        <h4>Menu</h4>
                        <li>
                            <Link to="/">Home</Link>
                        </li>

                        <li>
                            <Link to="/about">About</Link>
                        </li>
                        <li>
                            <Link to="/gallery">Gallery</Link>
                        </li>
                        <li>
                            <Link to="/vmap">Map</Link>
                        </li>
                        <li>
                            <Link to="/contact">Contact Us</Link>
                        </li>
                    </ul>
                </div>
                <div className="copyright">
                    <a href="#totop" className="totop">
                        Get back to top
                    </a>{" "}
                    - All rights reserved - &copy; 2021
                </div>
            </footer>
        </div>
    );
}

export default Footer;
