import React from "react";
import PreLoader from "../components/PreLoader";
import map from "../includes/img/map/map.svg";
import mapn from "../includes/img/map/map-northern.svg";
import mapw from "../includes/img/map/map-western.svg";
import maps from "../includes/img/map/map-southern.svg";
import hereicon from "../includes/img/map/here.svg";
import { useParams } from "react-router-dom";

function DisplayText() {
    let { location } = useParams();
    if (location === "northern") {
        return (
            <div className="centerize">
                Your location (<img src={hereicon} alt="Your Location on Map" />) is indicated on
                the map.
            </div>
        );
    } else if (location === "southern") {
        return (
            <div className="centerize">
                Your location (<img src={hereicon} alt="Your Location on Map" />) is indicated on
                the map.
            </div>
        );
    } else if (location === "western") {
        return (
            <div className="centerize">
                Your location (<img src={hereicon} alt="Your Location on Map" />) is indicated on
                the map.
            </div>
        );
    } else {
        return null;
    }
}

function VMap() {
    let { location } = useParams();
    let decider;
    if (location === "northern") {
        decider = mapn;
    } else if (location === "southern") {
        decider = maps;
    } else if (location === "western") {
        decider = mapw;
    } else {
        decider = map;
    }
    return (
        <React.Fragment>
            <PreLoader />
            <div className="container">
            {DisplayText()}
                <div className="maps">
                    <img src={decider} className="svgmap" alt="" />
                    <iframe
                        title="Tower Location on Earth"
                        src="https://www.google.com/maps/embed?pb=!1m16!1m12!1m3!1d1536.626461774368!2d55.16858006660901!3d37.257853556629975!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m1!1m0!5e1!3m2!1sen!2sfr!4v1640232143466!5m2!1sen!2sfr"
                        className="earthmap"
                        allowFullScreen=""
                        loading="lazy"
                    ></iframe>
                </div>
            </div>
        </React.Fragment>
    );
}

export default VMap;
