import React from "react";
import Tower from "../includes/img/front-page-tower.jpg";

function About() {
    return (
        <React.Fragment>
            <div className="container">
                <div className="about">
                    <h1>Gonbad-e Qabus</h1>
                    <h4>
                        <a href="https://whc.unesco.org/en/list/1398/">
                            (UNESCO World Heritage Site)
                        </a>
                    </h4>
                    <h3>Description</h3>
                    <p>
                        The 53 m high tomb built in ad 1006 for Qābus Ibn Voshmgir, Ziyarid ruler
                        and literati, near the ruins of the ancient city of Jorjan in north-east
                        Iran, bears testimony to the cultural exchange between Central Asian nomads
                        and the ancient civilization of Iran. The tower is the only remaining
                        evidence of Jorjan, a former centre of arts and science that was destroyed
                        during the Mongols’ invasion in the 14th and 15th centuries. It is an
                        outstanding and technologically innovative example of Islamic architecture
                        that influenced sacral building in Iran, Anatolia and Central Asia. Built of
                        unglazed fired bricks, the monument’s intricate geometric forms constitute a
                        tapering cylinder with a diameter of 17–15.5 m, topped by a conical brick
                        roof. It illustrates the development of mathematics and science in the
                        Muslim world at the turn of the first millennium AD.
                    </p>
                    <h3>Construction, Plan and Design</h3>
                    <p>
                        <img src={Tower} alt="" className="frontpagetower" />
                        The inscription bands on the tower, written in rhymed prose, state that
                        Qabus ordered the foundation of the tower built during his lifetime, in
                        1006/7. He was a prince of the Ziyarid dynasty, which was based in the
                        historic Tabaristan region of northern Iran. In the 11th century, this
                        region was still undergoing conversion from Zoroastrianism to Islam. The
                        foundation date on the monument is given in two calendar styles: Iranian
                        solar and Islamic lunar. The monument has an interior diameter of 9.67
                        metres (31.7 feet) at its base. According to Sheila S. Blair (2002), the
                        building's entrance contains some of the earliest evidence of the
                        development of the moqarnas structure (i.e. stalactite vaulting) in Iran. In
                        terms of design, the Gonbad-e Qabus tower resembles other cylindrical tomb
                        towers on Iran's Caspian Sea littoral. However, the Gonbad-e Qabus tower
                        differs from other examples because of its "extraordinary height". Taking
                        its conical roof into account, the tower measures c. 50 metres (160 feet)
                        above ground; this is three times its exterior diameter. Sheila S. Blair
                        notes: The tomb is entirely constructed of fine-quality baked brick whose
                        pale yellow color has been turned golden by the sun. The technical quality
                        of the construction is clear from its almost perfect survival despite the
                        ravages of time, climate, and even reported shelling by the Russians. The
                        only decoration comprises two inscription bands which ring the building
                        above the doorway and below the roof. Each band is divided into ten panels,
                        one set between each pair of buttresses. (...) The carefully planned text
                        combines with the tower's formal purity and soaring verticality to make it
                        one of the most famous and memorable monuments in all of Iranian
                        architecture. Oleg Grabar (1975) wrote that the Gonbad-e Qabus tower
                        "clearly belongs to the general category of a secular architecture for
                        conspicuous consumption". While discussing the forms of the tower tombs of
                        northern Iran (which includes the Gonbad-e Qabus tower), Grabar stated that
                        they may be connected with Zoroastrian funerary structures. According to
                        him, the link to Zoroastrian funerary structures is "strongly suggested". He
                        cites as examples the use of the Persian solar calendar in the inscription
                        on the Gonbad-e Qabus, as well as the occasional use of Middle Persian
                        (Pahlavi) on the other tomb towers of northern Iran. According to Melanie
                        Michailidis (2009), Zoroastrian influence is "manifestly present" in the
                        tower tombs of northern Iran, and can be seen in their height, purpose, and
                        forms. She argues that the towers were built by the Ziyarids and Bavandids
                        to emulate "the lost princely mausolea of the Sasanians". Though the tower
                        is meant to be Qabus's mausoleum, there is no body buried inside, similar to
                        the other tomb towers of northern Iran. According to legend, Qabus was
                        buried in a glass coffin, hanging from the roof by chains. Michailidis adds
                        that the "failure of the excavators to find a body might seem to lend
                        credence to this story". She explains that the towers were used in a
                        syncretic fashion. Bodies were placed inside the tombs, but lifted off the
                        ground, resting on a platform "composed of some impermeable material". As
                        Tabaristan was still undergoing Islamization at the time, syncretism would
                        be a logical explanation according to Michailidis. Qabus's tower meets
                        numerous criteria for disposing of the dead in the Zoroastrian way, but it
                        does not "obviously" fit the category of an "orthodox Zoroastrian way of
                        disposing of the dead", nor does it match a "proper Muslim burial".
                    </p>
                    <h3>Precise Location</h3>
                    <h4><a href="/vmap">Click here for more</a></h4>
                    <iframe
                        title="Tower Location on Earth"
                        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2181.5537652196836!2d55.16906779975661!3d37.257537029242!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3f82bf0f3681aac1%3A0xb8eacc0ae01e8b94!2sQabus%20Ibn%20Voshmgir%20Tower!5e0!3m2!1sen!2sfr!4v1640231351908!5m2!1sen!2sfr"
                        height="400"
                        allowFullScreen=""
                        loading="lazy"
                    ></iframe>
                </div>
            </div>
        </React.Fragment>
    );
}

export default About;
