import React, { ChangeEvent, FormEvent, useRef, useState } from "react";
import axios from "axios";
import ReCAPTCHA from "react-google-recaptcha";

type FormState = {
    email: string;
    name: string;
    message: string;
};

type ServiceMessage = {
    class: string;
    text: string;
};
function ContactUs() {
    const formId = "pxksz7Dp";
    const formSparkUrl = `https://submit-form.com/${formId}`;
    const recaptchaKey = "6LddkModAAAAAHaSPr2sUP7cacTLDt0_l6R23Pas";
    const recaptchaRef = useRef<any>();

    const initialFormState = {
        email: "",
        name: "",
        message: "",
    };

    const [formState, setFormState] = useState<FormState>(initialFormState);
    const [submitting, setSubmitting] = useState<boolean>(false);
    const [message, setMessage] = useState<ServiceMessage>();
    const [recaptchaToken, setReCaptchaToken] = useState<string>();

    const submitForm = async (event: FormEvent) => {
        event.preventDefault();
        setSubmitting(true);
        await postSubmission();
        setSubmitting(false);
    };

    const postSubmission = async () => {
        const payload = {
            ...formState,
            "g-recaptcha-response": recaptchaToken,
        };

        try {
            const result = await axios.post(formSparkUrl, payload);
            console.log(result);
            setMessage({
                class: "bg-green",
                text: "Thanks, someone will be in touch shortly.",
            });
            setFormState(initialFormState);
            recaptchaRef.current.reset();
        } catch (error) {
            console.log(error);
            setMessage({
                class: "bg-red",
                text: "Sorry, there was a problem. Please try again or contact support.",
            });
        }
    };

    const updateFormControl = (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const { id, value } = event.target;
        const key = id as keyof FormState;
        const updatedFormState = { ...formState };
        updatedFormState[key] = value;
        setFormState(updatedFormState);
    };

    const updateRecaptchaToken = (token: string | null) => {
        setReCaptchaToken(token as string);
    };

    return (
        <div>
            <div className="contact-form">
                <h2>Contact Us</h2>
                {message && <div className={`${message.class}`}>{message.text}</div>}
                <form onSubmit={submitForm}>
                    <div className="row">
                        <div className="col-25">
                            <label htmlFor="name">Name</label>
                        </div>
                        <div className="col-75">
                            <input
                                onChange={updateFormControl}
                                type="text"
                                id="name"
                                value={formState?.name}
                            />
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-25">
                            <label htmlFor="email">Email</label>
                        </div>
                        <div className="col-75">
                            <input
                                onChange={updateFormControl}
                                type="email"
                                id="email"
                                value={formState?.email}
                            />
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-25">
                            <label htmlFor="message">Message</label>
                        </div>
                        <div className="col-75">
                            <textarea
                                onChange={updateFormControl}
                                id="message"
                                value={formState?.message}
                            ></textarea>
                        </div>
                    </div>
                    <div className="row">
                        <div className="centerize">
                            <ReCAPTCHA
                                ref={recaptchaRef}
                                sitekey={recaptchaKey}
                                onChange={updateRecaptchaToken}
                            />
                        </div>
                    </div>

                    <div className="row">
                        <div className="centerize">
                            <button disabled={submitting}>
                                {submitting ? "Submitting..." : "Submit"}
                            </button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default ContactUs;
