import React from "react";

function Home() {
    return (
        <React.Fragment>
            <div className="container">
                <div className="mainpage">
                    <div>
                        <h2>Welcome to Qabus Tower</h2>
                        <h3>Most Asked Questions:</h3>
                        <h4>How tall is the tower?</h4>
                        <p>The 53-meter high tower is built on its 15-meter high hill.</p>
                        <h4>Where is the tower?</h4>
                        <p>It is located at the intersection of Imam Khomeini North and Jomhouri Street in Gonbad Kavous city.</p>
                        <h4>How much does it cost to visit it?</h4>
                        <p>It costs $5.00 for tourists and 30,000 Tomans for locals.</p>
                        <h4>Who built the Tower?</h4>
                        <p>It marks the grave of Ziyarid ruler Qabus (r. 978–1012), and was built during his lifetime in 1006/7</p>
                    </div>
                    <div>
                        <div className="ems">
                            <h3>Emergency Services</h3>
                            <h5>Click or tap to call!</h5>
                            <ul>
                                <a href="tel:+98110">
                                    <li>Police</li>
                                </a>
                                <a href="tel:+98115">
                                    <li>Emergency Medical Service</li>
                                </a>
                                <a href="tel:+98125">
                                    <li>Fire and Rescue</li>
                                </a>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default Home;
