import React from "react";
import { Link } from "react-router-dom";
import logo from "../includes/img/logo.png";
import "../includes/style/style.css";
import dayjs from "dayjs";

function toggleNav() {
    var x = document.getElementById("topnav");
    if (x.className === "toggle") {
        x.className += " responsive";
    } else {
        x.className = "toggle";
    }
}

function NavBar() {
    return (
        <div>
            <nav id="topnav" className="toggle">
                <div className="logo" id="totop">
                    <img src={logo} alt="" />
                    Qabus Tower Historical Monument
                </div>
                <ul>
                    <Link to="/">
                        <li>Home</li>
                    </Link>
                    <Link to="/about">
                        <li>About</li>
                    </Link>
                    <Link to="/gallery">
                        <li>Gallery</li>
                    </Link>
                    <Link to="/vmap">
                        <li>Map</li>
                    </Link>
                    <Link to="/contact">
                        <li>Contact</li>
                    </Link>
                    <a href="/#" className="icon" onClick={toggleNav}>
                        <i className="fa fa-bars"></i>
                    </a>
                </ul>
            </nav>
            <div className="dateandtime">
                <span className="blinker"></span>
                <b>{dayjs().format("DD MMMM YYYY - hh:mm A")}</b>
            </div>
        </div>
    );
}

export default NavBar;
