import React, { useEffect, useState } from "react";
import Lottie from "react-lottie";

import * as location from "../includes/img/preload-anim-earth.json";
import * as success from "../includes/img/preload-anim-success.json";

const defaultOptions1 = {
  loop: true,
  autoplay: true,
  animationData: location.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

const defaultOptions2 = {
  loop: true,
  autoplay: true,
  animationData: success.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

function PreLoader() {
  // eslint-disable-next-line 
  const [data, setData] = useState([]);
  const [loading, setloading] = useState(undefined);
  const [completed, setcompleted] = useState(undefined);

  useEffect(() => {
    setTimeout(() => {
      fetch("https://jsonplaceholder.typicode.com/posts")
        .then((response) => response.json())
        .then((json) => {
          setData(json);
          setloading(true);
          setTimeout(() => {
            setcompleted(true);
          }, 1000);
        });
    }, 900);
  }, []);

  return (
    <>
      {!completed ? (
        <>
            <div className="preloader">
          {!loading ? (
            <Lottie options={defaultOptions1} height={300} width={300} />
          ) : (
            <Lottie options={defaultOptions2} height={150} width={150} />
          )}</div>
        </>
      ) : (
        <>
          
        </>
      )}
    </>
  );
}

export default PreLoader;