import React, { useState, useEffect } from "react";
import sanityClient from "../client.js";

function Gallery() {
    const [allPhotos, setAllPhotos] = useState(null);

    useEffect(() => {
        sanityClient
            .fetch(
                `*[_type == "post"]{
                title,
                slug,
                mainImage{
                    asset->{
                        _id,
                        url
                    }
                }
            }`
            )
            .then((data) => setAllPhotos(data))
            .catch(console.error);
    }, []);

    return (
        <React.Fragment>
            <div className="container">
                <h2>Gallery</h2>
                <h4>Click to see it in fullscreen</h4>
                <div className="galleryContainer">
                    {allPhotos &&
                        allPhotos.map((post, index) => (
                            <div key={index} className="galleryItem gimg_small">
                                <a href={post.mainImage.asset.url} target="_blank" rel="noreferrer">
                                    <img src={post.mainImage.asset.url} title={post.title} alt={post.title} />
                                </a>
                            </div>
                        ))}
                </div>
            </div>
        </React.Fragment>
    );
}

export default Gallery;
